@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,200i,300,300i,400,400i,600,600i,700,700i,900,900i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:200,200i,300,300i,400,400i,600,600i,700,700i,900,900i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Alegreya:400,400i,700,700i&display=swap);
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: alegreya, 'Alegreya', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.75; }

h1, h2, h3, h4, h5, h6 {
  font-family: source-sans-pro, 'Source Sans Pro', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.0625em;
  line-height: 1.125em;
  font-weight: 900;
  font-size: 2rem;
  margin: 0 0 1.5rem 0; }
  h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color: inherit;
    text-decoration: none;
    transition: color 0.25s ease-in-out; }
    h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, h5 a:hover, h6 a:hover {
      color: #18bfef; }

p {
  margin: 0 0 1.5em; }

time {
  font-size: 0.875rem;
  margin: 0 0 0.75rem 0; }

code {
  font-family: source-code-pro, 'Source Code Pro', monospace; }

label {
  font-family: source-sans-pro, 'Source Sans Pro', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.0625em;
  line-height: 1.125em;
  font-weight: 900;
  display: block;
  font-size: 0.75rem;
  margin: 0 0 0.5rem 0; }

input[type='text'],
input[type='email'],
textarea {
  font-family: source-sans-pro, 'Source Sans Pro', sans-serif;
  font-weight: 300;
  width: 100%;
  font-size: 1rem;
  padding: 0 0.75em;
  background-color: #ffffff;
  border-style: solid;
  border-color: lightgray;
  border-width: 2px;
  line-height: 2.25rem;
  margin: 0 0 1.5rem 0; }

textarea {
  line-height: normal;
  line-height: initial; }

button.button,
a.button {
  font-family: source-sans-pro, 'Source Sans Pro', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.0625em;
  line-height: 1.125em;
  font-weight: 900;
  display: inline-block;
  width: 100%;
  border-width: 0.125rem;
  padding: 0 2.5rem;
  border-style: solid;
  border-color: black;
  background-color: transparent;
  font-size: 0.75em;
  color: inherit;
  text-decoration: none;
  line-height: 3.5em;
  transition: color 0.25s ease-in-out, border-color 0.25s ease-in-out; }
  button.button:hover,
  a.button:hover {
    color: #18bfef;
    border-color: #18bfef; }

img {
  display: block;
  width: 100%;
  object-fit: cover; }

a.img-portal {
  display: block;
  width: 100%;
  margin: 1.5rem 0;
  overflow: hidden; }
  a.img-portal img {
    transition: transform 0.25s; }
    a.img-portal img:hover {
      transform: scale(1.03125); }

#content {
  margin: 0 auto; }

#background {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: #98afc5;
  background-image: url(data:image/png;base64,), linear-gradient(45deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.25)), url(/static/media/background.e46e364f.jpg);
  background-position: center, center, top center;
  background-repeat: repeat, no-repeat, no-repeat;
  background-attachment: scroll, scroll, scroll;
  background-size: auto, auto, cover; }

#splash {
  height: calc(100vh - 7rem);
  padding: 3rem 1rem 1rem 1rem;
  color: whitesmoke;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end; }
  #splash h1 {
    font-size: 3rem; }
  #splash p {
    margin-top: 0;
    margin-bottom: 1.5rem;
    font-style: italic;
    font-weight: 400; }
  #splash a {
    color: inherit;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    -webkit-text-decoration-style: dotted;
            text-decoration-style: dotted;
    -webkit-text-decoration-skip-ink: none;
            text-decoration-skip-ink: none; }
    #splash a:hover {
      -webkit-text-decoration-line: none;
              text-decoration-line: none; }

#header {
  height: 18rem;
  position: relative;
  margin-top: -18rem;
  padding: 4rem;
  text-align: center;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  pointer-events: none; }
  #header * {
    pointer-events: auto; }

#navbar {
  height: 4rem;
  margin: -4rem 0 0 0;
  background: rgba(255, 255, 255, 0.125);
  color: whitesmoke;
  line-height: 4rem;
  white-space: nowrap;
  display: none; }
  #navbar ul {
    display: inherit;
    list-style: none;
    padding: 0;
    margin: 0; }
  #navbar a {
    color: inherit;
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 0.8em;
    font-weight: 900;
    letter-spacing: 0.075em; }
  #navbar .active {
    background-color: whitesmoke;
    color: black; }
  #navbar .links {
    flex-grow: 1; }
    #navbar .links a {
      padding: 0 2rem; }
  #navbar .icons {
    padding: 0 1rem; }
    #navbar .icons a {
      padding: 0 1rem; }

#tabloid {
  background-color: #ffffff; }

.story {
  padding: 2.5rem 2rem;
  text-align: center;
  border-top: solid 2px gainsboro; }
  .story h1 {
    font-size: 1.5rem; }
  .story p {
    text-align: justify; }
  .story time.date-published {
    display: block;
    width: 100%; }
  .story.major p {
    font-style: italic; }
  .story.major h1 {
    font-size: 3rem; }

#footer {
  padding: 2.5rem 2rem;
  border-top: solid 2px gainsboro;
  background-color: #f5f5f5; }

